@media screen and (max-width: 810px) {
	.contacts {
		form {
			padding: 0 12px;
			border: 2px solid #6f5643;
			max-height: 708px;
			font-weight: 400;
			font-size: 20px;
			line-height: 27px;
			.title-form {
				font-size: 24px;
				line-height: 30px;
			}
			.row {
				padding: 12px 12px 0;
			}
			.col-md-12 {
				margin-bottom: 28px;
				.btn {
					font-weight: 600;
					font-size: 20px;
					line-height: 27px;
				}
				label {
					font-weight: 400;
					font-size: 20px;
					line-height: 27px;
				}
				input {
					&::placeholder {
						font-weight: 400;
						font-size: 20px;
						line-height: 27px;
					}
				}
			}

			.title-form {
				margin-bottom: 0;
			}
			.col-md-6 {
				margin-bottom: 35px;
				label {
					margin-right: 39px;
					margin-top: 10px;
				}
				.ocklock {
					margin-right: 45px;
					margin-left: 0px;
				}
			}
			.col-md-9 {
				label {
					margin-top: 10px;
				}
			}
		}
	}
}
