@media screen and (max-width: 991px) {
	body {
		overflow-x: hidden;
	}
	.btn.btn-primary {
		font-family: "ProzaLibre";
		font-weight: 600;
		font-size: 20px;
		line-height: 27px;
		color: white;
		padding: 16px 100px;
		background: #e88360;
		border: 1px solid #d14614;
		border-radius: 16px;
		&:hover {
			transform: translate(0, 0);
		}
		&:active {
			background-color: #d14614;
		}
	}
	.title-second {
		margin-bottom: 16px;
		padding: 0;
		font-size: 24px;
		line-height: 30px;
	}
	.container {
		padding: 0 16px;
	}
	.template-title {
		margin-top: 60px;
		margin-bottom: 32px;
		font-family: "PlayfairDisplay";
		font-style: normal;
		font-weight: 700;
		font-size: 32px;
		line-height: 43px;
		color: #6f5643;

		padding: 8px 0;
		border: 2px solid $line-color;
		border-radius: 2px;
		border-right: none;
		border-left: none;
		position: relative;
		&::before {
			content: "";
			position: absolute;
			left: 0%;
			top: -10%;
			transform: translateY(-55%);
			max-width: 1376px;
			width: 100%;
			height: 3px;
			background-color: $primary-color;
			border-radius: 50px;
		}
	}

	.template-discription {
		font-family: "ProzaLibre";
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
	}
	header {
		.container {
			padding: 0 16px;
			.container-fluid {
				flex-direction: row;
			}
			.logo {
				max-width: 190px;
				margin: 0 auto;

				border: none;
				&::before {
					display: none;
				}
				.navbar-brand {
					margin: 0 51px;
					padding: 0;
					font-size: 24px;
					line-height: 30px;
					&:before {
						width: 1.2em;
						left: -25%;
					}
					&:after {
						width: 1.2em;
						left: 103%;
					}
				}
			}
			.navbar {
				margin: 0 auto 20px;
				border: none;

				&::after {
					content: "";
					position: absolute;
					left: 0%;
					top: 73px;
					transform: translateY(-55%);
					max-width: 1376px;
					width: 100%;
					height: 1px;

					background-color: $line-color;
					border-radius: 50px;
				}
				&::before {
					content: "";
					position: absolute;
					left: 0%;
					top: 68px;
					transform: translateY(-55%);
					max-width: 1376px;
					width: 100%;
					height: 2px;
					background-color: $primary-color;
					border-radius: 50px;
				}
				.nav-link {
					margin: 0;
				}
				.navbar-nav {
					margin-top: 30px;
				}
			}
		}
		.navbar-toggler {
			width: 44px;
			height: 44px;
			position: relative;
			background-image: url("data:image/svg+xml,%3Csvg width='44' height='44' viewBox='0 0 44 44' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0.5' y='0.5' width='43' height='43' rx='21.5' fill='%23E1B971'/%3E%3Crect x='0.5' y='0.5' width='43' height='43' rx='21.5' stroke='%236F5643'/%3E%3C/svg%3E");
			background-position: center;
			background-repeat: no-repeat;
			&:focus {
				box-shadow: none;
			}

			span {
				cursor: pointer;
				border-radius: 1px;
				height: 2px;
				width: 24px;
				background: #6f5643;
				position: absolute;
				left: 20%;
				top: 50%;
				display: block;
				content: "";
			}

			span:before,
			span:after {
				cursor: pointer;
				border-radius: 1px;
				height: 2px;
				width: 24px;
				background: #6f5643;
				position: absolute;
				left: 0%;
				top: 50%;
				display: block;
				content: "";
			}

			span:before {
				top: -5px;
			}

			span:after {
				top: 5px;
			}

			span,
			span:before,
			span:after {
				transition: all 0.5s ease-in-out;
			}
			&[aria-expanded="true"] {
				span {
					background-color: transparent;
				}
				span:before,
				span:after {
					top: 0;
				}
				span:before {
					transform: rotate(135deg);
				}
				span:after {
					transform: rotate(-135deg);
				}
			}
		}
	}

	.first-screen {
		.first-title {
			font-style: normal;
			font-weight: 400;
			font-size: 20px;
			line-height: 25px;
			color: #8a7768;
			padding: 0 16px;
		}
	}
	.about {
		background-position-y: 70px;
		padding-bottom: 20px;
		position: relative;
		background-size: cover;
		.about-template {
			flex-direction: column;
			align-items: center;

			.about-img {
				position: absolute;
				max-width: 158px;
				top: 63%;

				left: 64%;
			}
			.about-box {
				margin: 0;
				.about-box__item {
					margin: 22px auto 40px;
					padding: 24px 16px;
					max-width: 400px;
					border: 2px solid #d2a24c;
				}
			}
			.btn-box {
				max-width: 326px;
			}
			.btn.btn-primary {
				font-family: "ProzaLibre";
				font-weight: 600;
				font-size: 20px;
				line-height: 27px;
				color: white;
				padding: 16px 92px;
				background: #e88360;
				border: 1px solid #d14614;
				border-radius: 16px;
			}
		}
	}
	.slider {
		margin-top: 10px;
		.main-box-first {
			display: flex;
			flex-direction: column;
			margin-bottom: 32px;
		}
		.box-main-second {
			display: flex;
			flex-direction: column;

			.about_box {
				order: 2;

				.about-box__item {
					max-width: 352px;
					padding: 24px 16px;
					margin: 0;
					transform: translate(0px, -5px);
					border: 2px solid #d2a24c;
				}
			}
		}
		.slider-template {
			max-width: 358px;
			margin: 0 auto 11px;
		}
		.slider-first-img {
			max-width: 358px;
		}
		.slider-second-img {
			width: 108.67px;
			height: 88px;
			border-radius: 16px;
			border: 2px solid #6f5643;
		}
		.slider-template-second {
			margin: 0 auto 24px;
			max-width: 358px;
			position: relative;
			margin-left: 0;
			order: 1;
		}
		.slider-template__item-wrapper-second {
			margin: 0 4px;
		}
		.slick-arrow {
			width: 44px;
			height: 44px;
			left: 204px;
			top: -80px;
