				top: -80px;
			}
		}
	}
	.advantages {
		background-size: cover;

		.advantages-content {
			flex-direction: column;

			&__box:first-child {
				margin-right: 0px;
				margin-bottom: -108px;
			}
			&__box:last-child {
				margin-left: 0px;
				margin-top: -108px;
			}

			.text-img {
				max-width: 338px;
			}
		}
		.title-second {
			margin-left: 0px;
		}
		.template-discription {
			max-width: 340px;
		}
		.title-second.main-advantages {
			margin: 0 auto;
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 30px;
			text-align: center;
			color: #8a7768;
		}
	}
	.menu {
		.img-bg-firs {
			left: 59%;
			top: 113%;
			transform: rotate(-15deg);
			max-width: 150px;
			z-index: 0;
		}
		.img-bg-second {
			max-width: 145px;
			position: absolute;
			left: 68%;
			top: 107%;
			transform: rotate(24deg);
			z-index: -1;
		}
		.img-bg-third {
			display: none;
		}
		.menu-mainbox {
			flex-direction: column;
			justify-content: center;
			padding: 0;
			border: none;
			.margen-menu {
				margin-top: 0;
			}
			&__template {
				padding: 10px;
				min-width: 200px;
				border: 2px solid #6f5643;
				margin-bottom: 18px;
				&-name {
					font-size: 24px;
					line-height: 30px;
				}
				&-item {
					border-bottom: 1px solid #b19e90;
				}
			}
			.menu-fast {
				margin-left: 0;
			}
			&__template-item {
				.group-menu {
					font-weight: 400;
					font-size: 20px;
					line-height: 25px;
				}
				&-description {
					font-weight: 400;
					font-size: 16px;
					line-height: 22px;
				}
			}
		}
	}
	.contacts {
		.img-contacts {
			display: none;
		}
		.contacts-template {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 20px;

			&-item {
				&__title {
					padding-left: 40px;
					margin-bottom: 12px;
					font-weight: 400;
					font-size: 24px;
					line-height: 30px;
				}
				margin-right: 140px;

				&__info {
					text-align: start;
					font-weight: 400;
					font-size: 16px;
					line-height: 22px;
					margin-bottom: 20px;
				}
			}
		}
	}
	footer {
		.nav-footer {
			display: none;
		}
		.navbar-nav-img {
			margin-top: 28px;
			list-style: none;
			display: flex;
			align-items: center;
			padding: 0;
			.nav-link {
				width: 52px;
				height: 52px;
				margin-right: 38px;
			}
		}
		.navbar-brand {
			font-weight: 400;
			font-size: 20px;
			line-height: 25px;
			&:before {
				width: 1.2em;
				left: -25%;
			}
			&:after {
				width: 1.2em;
				left: 103%;
			}
		}
	}
	.popup {
		&__body {
			min-height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 15px 5px;
		}
		&__content {
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 22px 20px;
		}

		&__close {
			top: 4%;
			left: 88%;
		}
		&__title {
			margin: 20px 0;
			font-size: 26px;
			line-height: 25px;
			color: #6f5643;
		}
		&__subtitle {
			font-family: "ProzaLibre";
			font-style: normal;
			font-weight: 400;
			font-size: 19px;
			line-height: 25px;

			margin-bottom: 35px;
		}
	}
