.contacts {
	margin-bottom: 60px;
	position: relative;
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.contacts-template {
		display: flex;
		justify-content: center;
		margin-bottom: 60px;

		&-item {
			max-width: 260px;
			margin-right: 140px;

			&__title {
				background: no-repeat left top;
				padding-left: 60px;
				background-size: contain;
			}
			&-address {
				background-image: url("data:image/svg+xml,%3Csvg width='44' height='45' viewBox='0 0 44 45' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M27.7182 19.4036C27.7182 22.5569 25.1698 25.1236 21.9982 25.1236C18.8265 25.1236 16.2782 22.5753 16.2782 19.4036C16.2782 16.2319 18.8448 13.6836 21.9982 13.6836C22.6215 13.6836 23.2265 13.7753 23.7765 13.9586' stroke='%23E88360' stroke-width='4' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M10.9633 8.38345C18.975 0.848446 34.5033 3.43345 37.3633 16.1018C39.4717 25.4151 33.6783 33.2984 28.6 38.1751C26.8255 39.8866 24.4562 40.843 21.9908 40.843C19.5255 40.843 17.1562 39.8866 15.3817 38.1751C10.3217 33.2801 4.51 25.3968 6.63667 16.0834' stroke='%23E88360' stroke-width='4' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
			}
			&-hours {
				background-image: url("data:image/svg+xml,%3Csvg width='44' height='45' viewBox='0 0 44 45' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22 15.1667V24.3334' stroke='%23E88360' stroke-width='4' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M16.5 4.16675H27.5' stroke='%23E88360' stroke-width='4' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M9.16668 15.1667C7.07767 17.94 5.95116 21.3196 5.95835 24.7917C5.95835 33.6467 13.145 40.8333 22 40.8333C30.855 40.8333 38.0417 33.6467 38.0417 24.7917C38.0417 15.9367 30.855 8.75 22 8.75C19.69 8.75 17.5083 9.22667 15.5283 10.1067' stroke='%23E88360' stroke-width='4' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
			}
