				background-image: url("data:image/svg+xml,%3Csvg width='42' height='42' viewBox='0 0 42 42' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M16.215 10.0184C16.545 10.4767 16.7834 10.8984 16.9484 11.3017C17.1134 11.6867 17.205 12.0717 17.205 12.4201C17.205 12.8601 17.0767 13.3001 16.82 13.7217C16.5817 14.1434 16.2334 14.5834 15.7934 15.0234L14.4 16.4717C14.1984 16.6734 14.1067 16.9117 14.1067 17.2051C14.1067 17.3517 14.125 17.4801 14.1617 17.6267C14.2167 17.7734 14.2717 17.8834 14.3084 17.9934C14.6384 18.5984 15.2067 19.3867 16.0134 20.3401C16.8384 21.2934 17.7184 22.2651 18.6717 23.2367C19.6617 24.2084 20.615 25.1067 21.5867 25.9317C22.54 26.7384 23.3284 27.2884 23.9517 27.6184C24.0434 27.6551 24.1534 27.7101 24.2817 27.7651C24.4284 27.8201 24.575 27.8384 24.74 27.8384C25.0517 27.8384 25.29 27.7284 25.4917 27.5267L26.885 26.1517C27.3434 25.6934 27.7834 25.3451 28.205 25.1251C28.6267 24.8684 29.0484 24.7401 29.5067 24.7401C29.855 24.7401 30.2217 24.8134 30.625 24.9784C31.0284 25.1434 31.45 25.3817 31.9084 25.6934L37.9767 30.0017C38.4534 30.3317 38.7834 30.7167 38.985 31.1751C39.1684 31.6334 39.2784 32.0917 39.2784 32.6051C39.2784 33.2651 39.1317 33.9434 38.82 34.6034C38.5084 35.2634 38.105 35.8867 37.5734 36.4734C36.675 37.4634 35.685 38.1784 34.5667 38.6367C33.4667 39.0951 32.275 39.3334 30.9917 39.3334C29.1217 39.3334 27.1234 38.8934 25.015 37.9951C22.9067 37.0967 20.7984 35.8867 18.7084 34.3651C16.5782 32.8069 14.5681 31.091 12.695 29.2317C10.8411 27.3654 9.1312 25.3614 7.58002 23.2367C6.07669 21.1467 4.86669 19.0567 3.98669 16.9851C3.10669 14.8951 2.66669 12.8967 2.66669 10.9901C2.66669 9.74341 2.88669 8.55175 3.32669 7.45175C3.76669 6.33341 4.46335 5.30675 5.43502 4.39008C6.60835 3.23508 7.89169 2.66675 9.24835 2.66675C9.76169 2.66675 10.275 2.77675 10.7334 2.99675C11.21 3.21675 11.6317 3.54675 11.9617 4.02341' stroke='%23E88360' stroke-width='4' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
			}
			&:last-child {
				margin-right: 0;
			}
			&__title {
				font-family: "Lobster";
				font-style: normal;
				font-weight: 400;
				font-size: 36px;
				line-height: 45px;
				color: #6f5643;
				margin-bottom: 20px;
			}
			&__info {
				font-family: "ProzaLibre";
				font-style: normal;
				font-weight: 400;
				font-size: 24px;
				line-height: 33px;
				color: #8a7768;
				text-align: start;
			}
		}
	}
	form {
		max-width: 816px;
		max-height: 708px;
		border: 5px solid #6f5643;
		border-radius: 16px;
		padding: 40px;
		font-family: "ProzaLibre";
		font-style: normal;
		font-weight: 400;
		font-size: 24px;
		line-height: 33px;
		color: #8a7768;
		.title-form {
			font-family: "Lobster";
			font-style: normal;
			font-weight: 400;
			font-size: 36px;
			line-height: 45px;
			color: #6f5643;
			text-align: center;
			margin-bottom: 32px;
		}
		input {
			max-width: 100%;
		}
		input[type="number"]::-webkit-inner-spin-button,
		input[type="number"]::-webkit-outer-spin-button,
		input[type="number"] {
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
			margin: 0;
			-moz-appearance: textfield;
		}
		.border-form {
			border-bottom: 2px solid #6f5643;
		}
		.col-md-12 {
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-bottom: 50px;
			label {
				white-space: nowrap;
				font-weight: 400;
				font-size: 24px;
				line-height: 33px;
			}
			.btn {
				font-family: "ProzaLibre";

				font-weight: 600;
				font-size: 24px;
				line-height: 33px;
				color: white;
				padding: 19px 144px;
			}
		}

		input {
			width: 100%;
			background: transparent;
			border-radius: 10px;
			padding: 0px 50px;

			font-family: "ProzaLibre";
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 33px;
			color: #6f5643;
			border: none;
			&::placeholder {
				font-family: "ProzaLibre";
				font-style: normal;
				font-weight: 400;
				font-size: 24px;
				line-height: 33px;
				color: #b19e90;
			}
			&:focus {
				border: none;
				box-shadow: none;
				&::placeholder {
					color: transparent;
				}
			}
			&:focus-visible {
				outline: none;
			}
		}

		.col-md-6 {
			display: flex;
			padding: 0;
			margin-bottom: 40px;
			label {
				margin-top: 7px;
				margin-right: 20px;
				position: relative;
			}

			.timepicker {
				position: relative;
				padding: 7px 75px;
				caret-color: transparent;
				border-radius: 16px;
				background-image: url("data:image/svg+xml,%3Csvg width='240' height='52' viewBox='0 0 240 52' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M90 20V27.5' stroke='%238A7768' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M85.5 11H94.5' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M79.5 20C77.7908 22.2691 76.8691 25.0342 76.875 27.875C76.875 35.12 82.755 41 90 41C97.245 41 103.125 35.12 103.125 27.875C103.125 20.63 97.245 14.75 90 14.75C88.11 14.75 86.325 15.14 84.705 15.86' stroke='%238A7768' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M163.24 22.4417L155.633 30.0483C154.735 30.9467 153.265 30.9467 152.366 30.0483L144.76 22.4417' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Crect x='1' y='1' width='238' height='50' rx='11' stroke='%238A7768' stroke-width='2'/%3E%3C/svg%3E");
