					transition: 0.3s ease-in;
				}
				&:focus {
					background-image: url("data:image/svg+xml,%3Csvg width='240' height='52' viewBox='0 0 240 52' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M90 20V27.5' stroke='%238A7768' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M85.5 11H94.5' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M79.5 20C77.7908 22.2691 76.8691 25.0342 76.875 27.875C76.875 35.12 82.755 41 90 41C97.245 41 103.125 35.12 103.125 27.875C103.125 20.63 97.245 14.75 90 14.75C88.11 14.75 86.325 15.14 84.705 15.86' stroke='%238A7768' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M144.76 29.5583L152.367 21.9517C153.265 21.0533 154.735 21.0533 155.634 21.9517L163.24 29.5583' stroke='%238A7768' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3Crect x='1' y='1' width='238' height='50' rx='11' stroke='%238A7768' stroke-width='2'/%3E%3C/svg%3E");
					transition: 0.3s;
				}
			}

			.ocklock {
				margin-left: 60px;
			}

			input {
				border: 2px solid #6f5643;
				width: 240px;
				border-radius: 16px;
				font-family: "ProzaLibre";
				font-style: normal;
				font-weight: 400;
				font-size: 22px;
				line-height: 33px;
				background-color: inherit;
				color: #6f5643;
				&::placeholder {
					font-family: "ProzaLibre";
					font-style: normal;
					font-weight: 400;
					font-size: 24px;
					line-height: 33px;
					color: #b19e90;
				}
			}
		}
